@font-face {
  font-family: "Orator";
  src: url("./OratorStd.otf");
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Orator";
}

.Particles {
  z-index: -1;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.pointer{
  cursor: pointer;
}